@import url(https://fonts.googleapis.com/css?family=Montserrat+Subrayada);
@import url(https://fonts.googleapis.com/css?family=Denk+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrap{
	display: grid;
	grid-template-rows: repeat(5, auto);
	grid-template-columns: repeat(5, auto);
	grid-gap: 2em 1em;
	-webkit-justify-content: center;
	        justify-content: center;	
}

.body{
	position: absolute;
	width: 100vw;
	height: 100%;
	background-color: black;
	overflow-x: hidden;
	padding: 4rem 5rem 4rem 5rem;

}

.App {
  text-align: center;
}

h2{
	line-height: 5em;
	font-family: 'Denk One', sans-serif;
	text-shadow: -1px -1px 0px rgba(255,255,255,0.3), 1px 1px 0px rgba(0,0,0,0.8);
	color: #ffffff;
	opacity: 0.4;
	text-align: center;
} 

@media (min-width: 481px) and (max-width: 767px) {
	img:nth-last-child(1){
	grid-column-end: -3 ;
	}
}

img{
	width: 5em;
	height: 8em;
}

img#verso{ 
	background: url(/Memory_Game/static/media/399772.c9ef92fe.jpg);
	background-size: auto 100%;
	background-position: center;
	display: block;
 box-sizing: border-box;
  	padding-right: 5em; 
}

img:hover{
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	transition: ease-in-out;
}

#recto{
	box-shadow: 0px 0px 15px 1px white;
}

@media screen and (min-width: 500px) {
	img:nth-last-child(1){
	grid-column-end: -2 ;
	}
	img:nth-last-child(19){
	grid-column-end: -2;    
	}
 	img:nth-last-child(18){
	grid-column-start: 1;    
 	}
 	img:nth-last-child(3){
	grid-column-start: 2;   
 	}
 	img:nth-last-child(21){
	grid-column-start: 2;   
	}

}
  

@media (min-width: 100px) and (max-width: 767px) {
  
  img{
  	width: 4em;
	height: 6em;
  }
  img#verso{
  	padding-right: 4em;
  } 
}

.navcontainer{
	background-image: linear-gradient(#1a1a1a 80%, black); 
	height: 80px;
	width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	box-shadow: 0px 0px 15px 1px white;
}

.item{
	color: #ffde72;
	-webkit-flex-shrink: 0;
	        flex-shrink: 0;
	padding: 1px;
	margin-left: 20px;
	margin-top: 10px; 
}

h1.item {
	font-family: 'Montserrat Subrayada', sans-serif;
}

h2.item{
	font-family: 'Denk One', sans-serif;
	text-shadow: -1px -1px 0px rgba(255,255,255,0.3), 1px 1px 0px rgba(0,0,0,0.8);
	color: #ffffff;
	opacity: 0.4;
}

@media all and (max-width: 500px){

	.navcontainer{
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
		height: 160px;
	}
}
